<template>
  <div class="login" v-if="!loggedIn">
    <h1>Login</h1>
    <form @submit.prevent="login({user,pass})">
      <div>
        <input v-model="user" placeholder="user">
      </div>
      <div>
        <input v-model="pass" type="password" placeholder="password">
      </div>
      <div>
        <button>Login</button>
      </div>
    </form>
  </div>
  <div v-else>
    Logged in.
  </div>
</template>

<script>
import {mapState} from 'vuex'
import {mapActions} from 'vuex'
export default {
  name: 'Login',
  props: {},
  data() {
    return {
      user: '',
      pass: ''
    }
  },
  computed: {
    ...mapState(['loggedIn'])
  },
  methods: {
    ...mapActions(['increment', 'login'])
  }
}
</script>

