<template>
  <span class="clickable" @click="setGameModalRoute">
    <slot></slot>
  </span>
</template>

<script>
export default {
  name: 'GameModalLink',
  components: {},
  props: ['gameId'],
  methods: {
    setGameModalRoute() {
      this.$router.push({
        name: this.$route.name,
        params: this.$route.params,
        query: _.extend({}, this.$route.query, {
          showGame: this.gameId
        })
      })
    }
  }
}
</script>

