import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import _ from 'lodash'

Vue.use(Vuex)

function gameIdToUpdateId(id) {
  return id.substr(0, 13) + '800'
}

const store = new Vuex.Store({
  state: {
    loggedIn: false,
    isAdmin: false,
    username: null,
    search: null,
    sortByKey: 'nameSorting',
    sortByDir: 'asc',
    bShowPreloads: true,
    bGrabDLC: true,
    showPlayerCount: true,
    showReleaseDate: false,
    showSizeColumn: false,
    showPreloadColumn: false,
    showLanguagesColumn: false,
    showLikeChangedAtColumn: false,
    showLikesColumn: false,
    showGamesAsTable: true,
    filterWantedStatus: '', // Any
    filterLikeStatus: '', // Any
    filterDemoStatus: '', // Any
    filterPlayersMin: null,
    filterPlayersMax: null,
    filterInstalled: false,
    filterShowBlacklisted: false,
    filterHideRegions: [],
    filterLanguagesRequired: [],
    downloadedByID: {},
    gamesByID: {},
    dlcByGameID: {},
    versionByID: {},
    wantedByID: {},
    blacklistedById: {},
    likes: {},
    lastUpdateDB: null,
    lastFileScan: null,
    switchDataBySwitchId: {},
    currentSwitchId: null,
    history: null,
    hideNavBar: false,
    showcaseCurrentId: null, //'01000320000cc000'
    showcaseDelay: 30000
  },
  getters: {
    regionsFound: (state, getters) => {
      return _.filter(_.uniq(_.map(state.gamesByID, 'region')))
    },
    languagesFound: (state, getters) => {
      return _.filter(_.uniq(_.flatten(_.map(state.gamesByID, 'languages'))))
    },
    knowAnyGameKey: (state, getters) => {
      return _.some(state.gamesByID, 'key')
    },
    anyGameBlacklisted: (state, getters) => {
      return _.some(getters.combinedGames, 'blacklisted')
    },
    likesSumByBaseID: (state, getters) => {
      let users = _.keys(state.likes)
      let baseIds = _.uniq(
        _.flatten(
          _.map(state.likes, function(data) {
            return _.keys(data)
          })
        )
      )

      let out = {}
      _.each(baseIds, function(baseId) {
        // let sum  = _.sumBy(_.map(users, (user) => _.get(state.likes, [user, baseId, 'likeInt'])))
        let usersLike = _.filter(users, user => {
          return _.get(state.likes, [user, baseId, 'likeInt']) === 1
        })
        let usersDislike = _.filter(users, user => {
          return _.get(state.likes, [user, baseId, 'likeInt']) === -1
        })
        let likesScore = usersLike.length * users.length - usersDislike.length
        let likesRatedByCount = usersLike.length + usersDislike.length
        out[baseId] = {
          usersLike,
          usersDislike,
          likesRatedByCount,
          likesScore
        }
      })
      return out
    },
    combinedGames: (state, getters) => {
      let {wantedByID, blacklistedById, gamesByID, downloadedByID, dlcByGameID, versionByID, bShowPreloads, likes, username} = state

      return _.map(gamesByID, game => {
        let gameCombined = _.extend(
          {},
          game,
          {
            hasGame: false,
            wanted: !!wantedByID[game.id],
            blacklisted: !!blacklistedById[game.id],
            latestVersion: _.get(versionByID, [game.id]) || 0,
            nameSorting: _.toLower(game.name),
            languagesCount: _.size(game.languages),
            likeInt: _.get(likes, [username, game.id, 'likeInt']) || 0, // current user
            likeChangedAt: _.get(likes, [username, game.id, 'changedAt']) || 0,
            likesRatedByCount: _.get(getters.likesSumByBaseID, [game.id, 'likesRatedByCount']) || 0,
            usersLike: _.get(getters.likesSumByBaseID, [game.id, 'usersLike']) || [],
            usersDislike: _.get(getters.likesSumByBaseID, [game.id, 'usersDislike']) || [],
            likesScore: _.get(getters.likesSumByBaseID, [game.id, 'likesScore']) || -Infinity
          },
          _.get(downloadedByID, [game.id])
        )

        if (gameCombined.latestVersionLocal === 0) {
          // 1 so that games you have at v0 are ranked higher than games you don't have at ver ""
          gameCombined.latestVersionLocalSorting = 1
        } else {
          // 0 even if you don't have it, for sorting
          gameCombined.latestVersionLocalSorting = gameCombined.latestVersionLocal || 0
        }
        if (gameCombined.hasGame) {
          gameCombined.wanted = true
        }

        if (gameCombined.wanted) {
          if (gameCombined.latestVersion > (gameCombined.latestVersionLocal || 0)) {
            gameCombined.needsUpdate = true
          }
        }

        let switchInstalled = _.get(getters.currentSwitchData, 'installed')

        // DLC
        gameCombined.dlcs = _.mapValues(dlcByGameID[game.id], function(item, dlcID) {
          let dlc = _.extend(
            {
              hasDLC: false,
              blacklisted: !!blacklistedById[dlcID],
              installedVersion: _.get(switchInstalled, _.toUpper(dlcID))
            },
            item,
            _.get(downloadedByID, [gameCombined.id, 'dlcs', dlcID])
          )
          if (!dlc.blacklisted && (!dlc.hasDLC || dlc.latestVersion > (dlc.latestVersionLocal || 0))) {
            dlc.needsUpdate = true
          }
          return dlc
        })
        if (!bShowPreloads) {
          gameCombined.dlcs = _.reject(gameCombined.dlcs, 'preload')
        }
        gameCombined.dlcsBlacklistedCount = _.size(_.filter(gameCombined.dlcs, 'blacklisted'))
        gameCombined.dlcsNotBlacklisted = _.reject(gameCombined.dlcs, 'blacklisted')
        gameCombined.dlcTotalCount = _.size(gameCombined.dlcsNotBlacklisted)
        // DLC Current
        gameCombined.dlcUpToDate = _.reject(gameCombined.dlcsNotBlacklisted, {needsUpdate: true})
        gameCombined.dlcUpToDateCount = _.size(gameCombined.dlcUpToDate)
        gameCombined.dlcUpToDateUnlocked = _.reject(gameCombined.dlcUpToDate, 'preloaded')
        gameCombined.dlcPreloadedNotUnlockable = _.filter(gameCombined.dlcUpToDate, {preloaded: true, preload: true})
        gameCombined.dlcPreloadedUnlockable = _.filter(gameCombined.dlcUpToDate, {preloaded: true, preload: false})
        // DLC Outdated (old or missing)
        gameCombined.dlcNeededCount = _.size(_.filter(gameCombined.dlcsNotBlacklisted, {needsUpdate: true}))
        gameCombined.dlcWantedCount = _.size(_.filter(gameCombined.dlcsNotBlacklisted, {needsUpdate: true, hasDLC: false}))
        gameCombined.dlcOutdatedCount = _.size(_.filter(gameCombined.dlcsNotBlacklisted, {needsUpdate: true, hasDLC: true}))
        gameCombined.dlcWantedTooltip =
          'Wanted\n' +
          _.map(_.filter(gameCombined.dlcsNotBlacklisted, {needsUpdate: true, hasDLC: false}), function(dlc) {
            return `${dlc.name} (v${dlc.latestVersion})${dlc.preload ? '[No Key]' : ''}`
          }).join('\n')
        gameCombined.dlcOutdatedTooltip =
          'Outdated\n' +
          _.map(_.filter(gameCombined.dlcsNotBlacklisted, {needsUpdate: true, hasDLC: true}), function(dlc) {
            return `${dlc.name} (v${dlc.latestVersionLocal} < v${dlc.latestVersion})${dlc.preloaded ? '[PRELOADED]' : ''}`
          }).join('\n')
        gameCombined.dlcPreloadedNotUnlockableTooltip =
          'Preloaded\n' +
          _.map(gameCombined.dlcPreloadedNotUnlockable, function(dlc) {
            return `${dlc.name} (v${dlc.latestVersionLocal})[PRELOADED]`
          }).join('\n')
        gameCombined.dlcPreloadedUnlockableTooltip =
          'Unlockable\n' +
          _.map(gameCombined.dlcPreloadedUnlockable, function(dlc) {
            return `${dlc.name} (v${dlc.latestVersionLocal})[UNLOCKABLE]`
          }).join('\n')
        gameCombined.dlcUpToDateUnlockedTooltip =
          'Current\n' +
          _.map(gameCombined.dlcUpToDateUnlocked, function(dlc) {
            return `${dlc.name} (v${dlc.latestVersionLocal})`
          }).join('\n')
        gameCombined.dlcTotalTooltip =
          'DLC\n' +
          _.map(gameCombined.dlcsNotBlacklisted, function(dlc) {
            return `${dlc.name} (v${dlc.latestVersion})${dlc.preload ? '[PRELOAD]' : ''}`
          }).join('\n')

        // Status
        if (gameCombined.blacklisted) {
          gameCombined.status = 'blacklisted'
        } else if (gameCombined.hasGame) {
          if (gameCombined.latestVersion > gameCombined.latestVersionLocal) {
            gameCombined.status = 'outdated'
          } else if (gameCombined.latestVersion === gameCombined.latestVersionLocal) {
            if (state.bGrabDLC && gameCombined.dlcNeededCount > 0) {
              gameCombined.status = 'dlc-needed'
            } else if (gameCombined.dlcPreloadedUnlockable.length > 0) {
              gameCombined.status = 'unlockable'
            } else if (gameCombined.preloaded) {
              if (gameCombined.preload) {
                gameCombined.status = 'preloaded'
              } else {
                gameCombined.status = 'unlockable'
              }
              // } else if (gameCombined.dlcPreloadedNotUnlockable.length > 0) {
              //   gameCombined.status = 'dlc-preloaded'
            } else {
              gameCombined.status = 'current'
            }
          } else {
            gameCombined.status = 'future'
          }
        } else if (gameCombined.wanted) {
          gameCombined.status = 'wanted'
        }

        // Switch Status
        if (switchInstalled) {
          let baseGameInstalled = switchInstalled[gameCombined.idUpper]
          // gameCombined.installedVersion = switchInstalled[gameIdToUpdateId(gameCombined.idUpper)]
          gameCombined.installedVersion = switchInstalled[gameCombined.idUpper]

          if (_.isNumber(baseGameInstalled)) {
            if (gameCombined.installedVersion < gameCombined.latestVersionLocal) {
              gameCombined.switchStatus = 'outdated'
            } else {
              // all installed always reported as ver 0 ?
              // let anyOutdated = _.some(gameCombined.dlcs, function(dlc){
              //   return dlc.hasDLC && !dlc.preloaded && _.isNumber(dlc.installedVersion) && dlc.installedVersion < dlc.latestVersionLocal
              // })
              let missingDLC = _.filter(gameCombined.dlcs, function(dlc) {
                return dlc.hasDLC && !dlc.preloaded && !_.isNumber(dlc.installedVersion)
              })

              if (missingDLC.length > 0) {
                gameCombined.switchStatus = 'dlc-needed'
                gameCombined.switchMissingDLC = '\nMissing DLC:\n' + _.map(missingDLC, 'name').join('\n')
              } else if (gameCombined.installedVersion === gameCombined.latestVersionLocal) {
                gameCombined.switchStatus = 'current'
              } else {
                gameCombined.switchStatus = 'future'
              }
            }
          } else {
            gameCombined.switchStatus = ''
          }
        }

        return gameCombined
      })
    },
    filteredGames: (state, getters) => {
      let search = _.toLower(state.search)
      let results = getters.combinedGames
      // remove all with no name
      results = _.filter(results, 'name')

      if (!state.filterShowBlacklisted) {
        results = _.reject(results, 'blacklisted')
      }
      if (!state.bShowPreloads) {
        results = _.reject(results, 'preload')
      }
      if (state.filterInstalled && getters.currentSwitchData) {
        results = _.filter(results, 'switchStatus')
      }
      if (_.size(state.filterHideRegions) > 0) {
        results = _.filter(results, game => {
          return !_.includes(state.filterHideRegions, game.region)
        })
      }
      if (_.size(state.filterLanguagesRequired) > 0) {
        results = _.filter(results, game => {
          return _.every(state.filterLanguagesRequired, requiredLang => {
            return _.includes(game.languages, requiredLang)
          })
        })
      }

      if (state.filterPlayersMin) {
        results = _.filter(results, function(game) {
          return game.numberOfPlayers >= state.filterPlayersMin
        })
      }
      if (state.filterPlayersMax) {
        results = _.filter(results, function(game) {
          return game.numberOfPlayers <= state.filterPlayersMax
        })
      }

      if (state.filterDemoStatus) {
        results = _.filter(results, {isDemo: state.filterDemoStatus === 'onlyDemos'})
      }

      if (state.filterWantedStatus) {
        // see optionsHaveWant in NavBar
        results = _.filter(results, game => {
          if (state.filterWantedStatus === 'missing') {
            return game.status === 'wanted' || game.needsUpdate || game.preloaded || (state.bGrabDLC && game.wanted && game.dlcNeededCount > 0)
          } else if (state.filterWantedStatus === 'haveWant') {
            return game.wanted
          } else if (state.filterWantedStatus === 'unwanted') {
            return !game.wanted
          } else if (state.filterWantedStatus === 'have') {
            return game.hasGame
          }
        })
      }

      if (state.filterLikeStatus) {
        results = _.filter(results, game => {
          if (state.filterLikeStatus === 'liked') {
            return game.likeInt === 1
          } else if (state.filterLikeStatus === 'neutral') {
            return game.likeInt === 0
          } else if (state.filterLikeStatus === 'notDisliked') {
            return game.likeInt >= 0
          }
        })
      }

      if (!search) {
        return results
      } else {
        return _.filter(results, game => {
          return _.includes(_.toLower(game.name), search) || _.includes(_.toLower(game.id), search)  || _.includes(_.toLower(game.publisher), search)
        })
      }
    },
    shownGames: (state, getters) => {
      if (state.sortByKey === 'name' && state.sortByDir === 'asc') {
        return getters.filteredGames
      }
      return _.orderBy(getters.filteredGames, [state.sortByKey], [state.sortByDir])
    },
    currentSwitchData: (state, getters) => {
      return _.get(state.switchDataBySwitchId, state.currentSwitchId)
    }
  },
  mutations: {
    // {myKey: val, ...}
    set(state, stuff) {
      _.extend(state, stuff)
      saveState()
    },
    setSortByKeyDir(state, [key, dir]) {
      state.sortByKey = key
      state.sortByDir = dir
    },
    setGameData(state, {downloadedByID, gamesByID, dlcByGameID, versionByID, wantedByID, blacklistedById, likes}) {
      state.downloadedByID = downloadedByID.data
      state.gamesByID = gamesByID.data
      state.dlcByGameID = dlcByGameID.data
      state.versionByID = versionByID.data
      state.wantedByID = wantedByID.data
      state.blacklistedById = blacklistedById.data
      state.likes = likes.data
    },
    toggleHideNavBar(state) {
      state.hideNavBar = !state.hideNavBar
    },
    setSearch(state, search) {
      state.search = search
      location.hash = '#/'
    }
  },
  actions: {
    // increment({commit}) {
    //   commit("increment");
    // },
    logout({commit}) {
      commit('set', {loggedIn: false})
    },
    login({commit}, {user, pass}) {
      axios
        .post('/api/login', {user, pass})
        .then(function(response) {
          commit('set', {loggedIn: response.data.success, username: response.data.user, isAdmin: response.data.admin})
        })
        .catch(function(error) {
          console.log(error)
        })
    },
    fetchGamesData({commit, dispatch}) {
      console.log('fetching games data')
      axios
        .all([
          axios.get('/api/downloadedByID', {headers: {'x-api-key': params.apiKey}}),
          axios.get('/api/gamesByID', {headers: {'x-api-key': params.apiKey}}),
          axios.get('/api/dlcByGameID', {headers: {'x-api-key': params.apiKey}}),
          axios.get('/api/versionByID', {headers: {'x-api-key': params.apiKey}}),
          axios.get('/api/wantedByID', {headers: {'x-api-key': params.apiKey}}),
          axios.get('/api/blacklistedById', {headers: {'x-api-key': params.apiKey}}),
          axios.get('/api/likes', {headers: {'x-api-key': params.apiKey}})
        ])
        .then(
          axios.spread(function(downloadedByID, gamesByID, dlcByGameID, versionByID, wantedByID, blacklistedById, likes) {
            // All requests are now complete
            commit('setGameData', {downloadedByID, gamesByID, dlcByGameID, versionByID, wantedByID, blacklistedById, likes})
          })
        )
        .catch(function(error) {
          console.log('error', error)
        })
      dispatch('fetchSwitchData')
    },
    fetchSwitchData({commit}) {
      console.log('fetching switch data')
      axios.get('/api/switch', {headers: {'x-api-key': params.apiKey}}).then(function(response) {
        commit('set', {switchDataBySwitchId: response.data})
      })
    },
    fetchHistory({commit}) {
      axios
        .get('/api/history')
        .then(function(response) {
          console.log(response.data)
          commit('set', response.data)
        })
        .catch(function(error) {
          console.log(error)
        })
    },
    toggleWantedGame({commit, state}, {game}) {
      // param is just game ID. We could here make sure we add the update + dlc, if desired for nut purposes
      if (game.hasGame && game.wanted) {
        console.log('can not unwant game you have')
      } else if(!state.isAdmin){
        alert('Only Admins can Want games')
      } else {
        let change = {
          [game.id]: !game.wanted
        }
        let wantedByID = _.extend({}, state.wantedByID, change)
        commit('set', {wantedByID})
        axios.post('/api/wantedByID', change).catch(function(error) {
          console.log(error)
        })
      }
    },
    setLikeGame({commit, state}, {game, likeInt}) {
      let body = {
        baseId: game.id,
        likeInt
      }
      axios
        .post('/api/like', body)
        .then(function(response) {
          commit('set', {likes: response.data})
        })
        .catch(function(error) {
          console.log(error)
        })
    },
    deleteHistory({commit, state, dispatch}, {type}) {
      axios
        .post('/api/deleteHistory', {type})
        .then(function(response) {
          commit('set', {history: response.data.history})
        })
        .catch(function(error) {
          console.log(error)
        })
    }
  }
})

const STATE_TO_SAVE = [
  'username',
  'showPlayerCount',
  'showReleaseDate',
  'bGrabDLC',
  'showPreloadColumn',
  'showSizeColumn',
  'showLanguagesColumn',
  'showLikeChangedAtColumn',
  'showLikesColumn',
  'filterWantedStatus',
  'filterDemoStatus',
  'bShowPreloads',
  'currentSwitchId',
  'filterInstalled',
  'filterHideRegions'
  // 'filterLanguagesRequired' // weird bug
]

function parseQuery(queryString) {
  var query = {}
  var pairs = (queryString[0] === '?' ? queryString.substr(1) : queryString).split('&')
  for (var i = 0; i < pairs.length; i++) {
    var pair = pairs[i].split('=')
    query[decodeURIComponent(pair[0])] = decodeURIComponent(pair[1] || '')
  }
  return query
}

function saveState() {
  let state = _.pick(store.state, STATE_TO_SAVE)
  localStorage.setItem('state', JSON.stringify(state))
}

function loadState(params) {
  let state = JSON.parse(localStorage.getItem('state'))
  state = _.pick(state, STATE_TO_SAVE)
  // anything from the url gets put into the state
  _.extend(state, params)
  store.commit('set', state)
}
let params = parseQuery(location.hash.substr(_.indexOf(location.hash, '?') + 1))
// location.hash = _.first(_.split(location.hash, '?'))
loadState(params)

// Check if we're already logged in right away
axios.post('/api/user', {apiKey: params.apiKey}).then(function(response) {
  store.commit('set', {loggedIn: response.data.loggedIn, username: response.data.user, isAdmin: response.data.admin})
})

export default store
