import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import moment from 'moment'
import BootstrapVue from 'bootstrap-vue/dist/bootstrap-vue.min'
import VueHotkey from 'v-hotkey'
import infiniteScroll from 'vue-infinite-scroll'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import 'vue-material-design-icons/styles.css'

Vue.use(BootstrapVue)
Vue.use(infiniteScroll)
Vue.use(VueHotkey)

Vue.filter('formatDateTime', function(value) {
  if (value) {
    return moment(value).format('lll')
  }
})
Vue.filter('formatDate', function(value) {
  if (value) {
    return moment(value).format('L')
  }
})
Vue.filter('formatDateAsString', function(value) {
  if (value) {
    return moment(String(value)).format('L')
  }
})
Vue.filter('formatBytes', function(a, b) {
  // https://stackoverflow.com/questions/15900485/correct-way-to-convert-size-in-bytes-to-kb-mb-gb-in-javascript
  if (a === 0) return '0 Bytes'
  else if (!a) return ''
  let c = 1024,
    d = b || 2,
    e = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'],
    f = Math.floor(Math.log(a) / Math.log(c))
  return parseFloat((a / Math.pow(c, f)).toFixed(d)) + ' ' + e[f]
})
Vue.filter('formatTime', function(sec) {
  // https://stackoverflow.com/questions/15900485/correct-way-to-convert-size-in-bytes-to-kb-mb-gb-in-javascript
  if (sec === Infinity) {
    return sec
  }
  let d = Number(sec)
  let h = Math.floor(d / 3600)
  let m = Math.floor((d % 3600) / 60)
  let s = Math.floor((d % 3600) % 60)

  let hDisplay = h > 0 ? h + (h === 1 ? ' hour, ' : ' hours, ') : ''
  let mDisplay = m > 0 ? m + (m === 1 ? ' minute, ' : ' minutes, ') : ''
  let sDisplay = s > 0 ? s + (s === 1 ? ' second' : ' seconds') : ''
  return hDisplay + mDisplay + sDisplay
})
Vue.filter('humanizeVer', function(value, defaultNone = '') {
  if (!value) return defaultNone
  return value >> 16
})
Vue.filter('humanizeVerZero', function(value) {
  if (!value) return '0'
  return value >> 16
})
Vue.filter('googleUrl', function(game) {
  return `https://www.google.com/search?q=${encodeURIComponent(game.name)} game`
})
Vue.filter('eShopUrl', function(game) {
  if (game.region) {
    return `https://ec.nintendo.com/apps/${game.id}/${game.region}`
  } else {
    return `https://www.nintendo.com/search/#/results/${encodeURIComponent(game.name)}`
  }
})

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
