<template>

  <div v-if="shouldShow && !game" class="game-overlay loading" @click.self="shouldShow = false">
    Loading game info [{{gameId}}]...
  </div>

  <div v-else-if="shouldShow" class="game-overlay"
       :style="`background-image: url('api/banner/${game.idUpper}')`"
       @click.self="shouldShow = false" >

    <div class="game-holder">

      <div class="close-button clickable" @click="shouldShow = false">
        <close-outline-icon title="Close" />
      </div>

      <div class="icon-image-holder black-border">
        <div class="icon-image" v-bind:style="`background-image: url('api/icon/${game.idUpper}')`" @click="shouldShow = false"></div>
        <div class="game-info">
          <div class="info-holder">
            <GameNameHeader v-bind:game="game" />
            <div class="publisher">
              {{game.publisher}}
            </div>
            <div class="game-players" v-if="game.numberOfPlayers > 0">
              {{game.numberOfPlayers}} {{game.numberOfPlayers === 1 ? 'Player' : 'Players'}}
            </div>
          </div>
          <div class="action-holder">
            <a :href="game | googleUrl" target="_blank">Google</a>
            <a :href="game | eShopUrl" target="_blank">eShop</a>
            <GameActionIcons v-bind:game="game"/>
          </div>
        </div>
      </div>


      <!--<div class="text-left">-->
        <!--<GameNameHeader v-bind:game="game" />-->
      <!--</div>-->
      <!--<div class="text-left">-->
        <!--<h3>-->
          <!--{{game.intro}}-->
        <!--</h3>-->
          <!--{{game.description}}-->
      <!--</div>-->

      <b-tabs variant="black" class="game-tabs black-border">
        <b-tab title="Screenshots" active>

          <div class="screenshots-holder">
            <b-carousel id="carousel1"
                        style="text-shadow: 1px 1px 2px #333;"
                        controls
                        indicators
                        background="#ababab"
                        :interval="3000"
            >
              <b-carousel-slide :img-src="`api/screenshot/${game.idUpper}/${i}`" v-for="i in [0,1,2,3,4,5]">
              </b-carousel-slide>
            </b-carousel>
          </div>

        </b-tab>
        <b-tab title="Description" >
          <div class="text-left" style="margin: 15px">
            <h2>{{game.intro}}</h2>
            {{game.description}}
          </div>
        </b-tab>
        <!--<b-tab title="Content" >-->
          <!--<br>I'm a tab content-->
        <!--</b-tab>-->
      </b-tabs>

    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
import CheckIcon from 'vue-material-design-icons/Check'
import KeyIcon from 'vue-material-design-icons/Key'
import InformationOutlineIcon from 'vue-material-design-icons/InformationOutline'
import CloseIcon from 'vue-material-design-icons/Close'
import GameNameHeader from './GameNameHeader'
import GameActionIcons from './GameActionIcons'
import CloseOutlineIcon from 'vue-material-design-icons/CloseOutline'

export default {
  name: 'GameModal',
  components: {CloseOutlineIcon, GameNameHeader, CloseIcon, InformationOutlineIcon, CheckIcon, KeyIcon, GameActionIcons},
  props: ['gameId'],
  computed: {
    ...mapGetters(['combinedGames']),
    game() {
      return _.find(this.combinedGames, {id: _.toLower(this.gameId)})
    },
    shouldShow: {
      get() {
        return !!this.gameId
      },
      set(shouldShow) {
        if (!shouldShow) {
          this.$router.push({
            name: this.$route.name,
            params: this.$route.params,
            query: _.omit(this.$route.query, 'showGame')
          })
        }
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.game-overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background: black;
  background-size: cover;

  &.loading{
    color: white;
  }
}
.game-holder {
  overflow: hidden;
  background-size: cover;
  position: relative;
  height: 100vh;
  width: 100vw;
  color: white;
}
.black-border {
  background: rgba(0, 0, 0, 0.7);
  border: 1px solid black;
}
.close-button {
  width: 5vw;
}
.icon-image-holder {
  position: absolute;
  top: 3vh;
  left: 3vw;
  right: 3vw;
  display: flex;
  height: 20.2vh;

  .icon-image {
    background-size: cover;
    width: 20vh;
    height: 20vh;
  }
  .game-info {
    flex-basis: 50%;
    flex-grow: 1;
    color: #fff;
    font-size: 20px;
    font-weight: bold;
    text-align: left;
    padding: 5px 5px 5px 20px;
    display: flex;
    flex-direction: column;

    .info-holder {
      flex-grow: 1;
      overflow-y: auto;
      .publisher {
        color: #a5a5a5;
      }
      .game-players {
        color: lightgray;
      }
    }
    .action-holder {
      display: flex;
      justify-content: space-evenly;
      font-size: 15px;
      text-decoration: underline;
      > a {
        color: #85e8c7;
        margin-right: 10px;
      }
    }
  }
}

.game-tabs {
  position: absolute;
  bottom: 3vh;
  left: 3vw;
  height: 70vh;
  width: 94vw;

  .screenshots-holder {
    max-width: 116.7vh;
    margin: auto;
  }
}
</style>
