<template>
  <div>
    <!--<button type="button" title="Reload Files" class="btn btn-info btn-sm">BUTTON</button>-->

    <div class="games-table-holder" v-bind:class="hideNavBar ? '' : 'full-view-hight-under-navbar'" v-infinite-scroll="visuallyLoadMoreGames" infinite-scroll-distance="1000" infinite-scroll-throttle-delay="100">
      <div>
      <table class="games-table">
        <thead>
        <tr>
          <th class="clickable" @click="setSortBy('switchStatus')" v-if="currentSwitchData">Switch</th>
          <th class="clickable" @click="setSortBy('status')">Library</th>
          <th class="clickable table-left" @click="setSortBy('nameSorting')" v-bind:title="`${shownGames.length} Game(s)`">Name</th>
          <th class="clickable table-right" @click="setSortBy('numberOfPlayers')" v-if="showPlayerCount">Players</th>
          <th class="clickable table-right" @click="setSortBy('languagesCount')" v-if="showLanguagesColumn">Languages</th>
          <th class="clickable" @click="setSortBy('releaseDate')" v-if="showReleaseDate">Release</th>
          <!--<th class="clickable text-center" @click="setSortBy('latestVersionLocalSorting')">Downloaded Ver</th>-->
          <th class="clickable text-center table-right" @click="setSortBy('latestVersion')">Version</th>
          <th class="clickable text-center table-right" @click="setSortBy('dlcTotalCount')" v-if="bGrabDLC">DLC</th>
          <th class="clickable" @click="setSortBy('size')" v-if="showSizeColumn">Size</th>
          <th class="clickable text-center" @click="setSortBy('preload')" v-if="showPreloadColumn && bShowPreloads">Key</th>
          <th class="clickable" @click="setSortBy('likeChangedAt')" v-if="showLikeChangedAtColumn">Liked At</th>
          <th class="clickable text-center table-right" @click="setSortBy('likesScore')" v-if="showLikesColumn">Likes</th>
          <th class="clickable" @click="setSortBy('likeInt')" ></th>
        </tr>
        </thead>

        <tbody v-if="showGamesAsTable">
        <tr v-for="game in visuallyLoadedShownGames" class="game-row status-color" v-bind:class="game.status" v-bind:key="game.id">
          <td v-if="currentSwitchData" :title="game.installedVersion +' '+(game.switchMissingDLC || '')" class="status-color" :class="game.switchStatus">
            {{ game.switchStatus }}
          </td>
          <td @click="toggleWantedGame({game})" v-bind:class="game.hasGame ? '' : 'clickable'" v-bind:title="`${game.id}`">
            <span>
              {{ game.status }}
            </span>
          </td>
          <td class="table-left" v-bind:title="`${game.fileName || ''}\n${game.fileNameUpdate || ''}`">
            <GameModalLink v-bind:game-id="game.id">
              <span v-bind:class="game.preloaded ? 'font-italic' : ''">
                <GameNameHeader v-bind:game="game" />
              </span>
            </GameModalLink>
          </td>
          <td v-if="showPlayerCount" class="table-right" v-bind:title="`${game.intro || ''}`">
            {{game.numberOfPlayers}}
          </td>
          <td v-if="showLanguagesColumn" class="table-right" v-bind:title="game.languages.join(' ')">
            {{game.languagesCount}}
          </td>
          <td v-if="showReleaseDate" class="text-center" v-bind:title="`${game.description || ''}`">
            {{game.releaseDate | formatDateAsString}}
          </td>
          <td class="table-right" v-bind:title="`${game.latestVersionLocal || '0'} / ${game.latestVersion}`">
            <span v-if="currentSwitchData && game.switchStatus !== 'current' && game.switchStatus" v-bind:title="`Switch: ${game.installedVersion}`">
              ({{game.installedVersion | humanizeVerZero}})
            </span>
            <span v-if="game.latestVersion === 0 && !game.latestVersionLocal">
            </span>
            <span v-else-if="game.wanted && game.latestVersionLocal === game.latestVersion">
              {{game.latestVersion | humanizeVer}}
            </span>
            <span v-else-if="!game.wanted && !game.latestVersionLocal">
              {{game.latestVersion | humanizeVer}}
            </span>
            <span v-else>
              {{game.latestVersionLocal | humanizeVerZero}} / {{game.latestVersion | humanizeVerZero}}
            </span>
          </td>
          <td class="dlc-column table-right" v-if="bGrabDLC" v-bind:class="game.wanted && game.dlcUpToDateCount < game.dlcTotalCount ? 'stale' : ''">
              <span v-if="game.dlcTotalCount == 0">
              </span>
            <span v-else-if="game.dlcUpToDateUnlocked.length === game.dlcTotalCount">
                <span class="current-count" v-bind:title="game.dlcUpToDateUnlockedTooltip">{{game.dlcUpToDateUnlocked.length}}</span>
              </span>
            <!-- you have nothing yet -->
            <span v-else-if="game.dlcUpToDate.length === 0 && game.dlcOutdatedCount === 0 && !game.wanted">
                <span v-bind:title="game.dlcTotalTooltip">{{game.dlcTotalCount}}</span>
              </span>
            <span v-else>
                <span v-if="game.dlcWantedCount > 0">
                  <span class="wanted-count" v-bind:title="game.dlcWantedTooltip">{{game.dlcWantedCount}}</span> +
                </span>
                <span v-if="game.dlcOutdatedCount > 0">
                  <span class="outdated-count" v-bind:title="game.dlcOutdatedTooltip">{{game.dlcOutdatedCount}}</span> +
                </span>
                <span v-if="game.dlcPreloadedNotUnlockable.length > 0">
                  <span class="not-unlockable-count" v-bind:title="game.dlcPreloadedNotUnlockableTooltip">{{game.dlcPreloadedNotUnlockable.length}}</span> +
                </span>
                <span v-if="game.dlcPreloadedUnlockable.length > 0">
                  <span class="unlockable-count" v-bind:title="game.dlcPreloadedUnlockableTooltip">{{game.dlcPreloadedUnlockable.length}}</span> +
                </span>
                <span class="current-count" v-bind:title="game.dlcUpToDateUnlockedTooltip">{{game.dlcUpToDateUnlocked.length}}</span>
              </span>
            <span v-if="game.dlcsBlacklistedCount > 0" v-bind:title="`${game.dlcsBlacklistedCount} DLC(s) blacklisted`">
                *
              </span>
          </td>
          <td v-if="showSizeColumn" class="text-center" :title="game.size">
            <span v-if="game.size > 0">
              {{game.size | formatBytes}}
            </span>
          </td>
          <td class="text-center" v-if="showPreloadColumn && bShowPreloads">
              <span class="actions-container">
                <span v-if="game.preloaded && !game.preload" title="Preloaded - Unlockable!">
                  <key-icon title="Preloaded - Unlockable!" />
                </span>
                <span v-else-if="game.preload" class="action-item " title="Key Unknown">
                  <lock-icon title="Key Unknown" />
                </span>
                <!--<span v-if="game.dlcPreloadedUnlockable.length > 0 || game.dlcPreloadedNotUnlockable.length > 0">-->
                <!--DLC-->
                <!--</span>-->
              </span>
          </td>
          <td v-if="showLikeChangedAtColumn" class="text-center" :title="game.likeChangedAt | formatDateTime">
            {{game.likeChangedAt | formatDate}}
          </td>
          <td v-if="showLikesColumn" class="table-right" :title="`Liked by: ${game.usersLike.join(', ')}\nDisliked by: ${game.usersDislike.join(', ')}`">
            <span v-if="game.likesRatedByCount > 0">
              {{game.usersLike.length}} / {{game.likesRatedByCount}}
            </span>
          </td>
          <td>
            <GameActionIcons v-bind:game="game" />
          </td>
        </tr>
        </tbody>
      </table>
      </div>
      <div v-if="!showGamesAsTable" class="game-grid">
        <div v-for="game in visuallyLoadedShownGames" class="game-grid-box"
             v-bind:class="game.status"
             v-bind:title="`[${game.id}] ${game.status || ''} - ${game.intro}`"
             v-bind:key="game.id"
        >
          <div class="game-grid-box-placeholder">
            {{game.name}}
          </div>
          <GameModalLink v-bind:game-id="game.id">
            <div class="game-grid-box-image" v-bind:style="`background-image: url('api/icon/${game.idUpper}')`">
              <div class="game-players game-grid-box-hidable" v-if="game.numberOfPlayers > 0">
                {{game.numberOfPlayers}}P
              </div>
              <div class="game-preload game-grid-box-hidable" v-if="game.preloaded" title="Preloaded">
                <lock-icon title="Preloaded"/>
              </div>
              <div class="game-name game-grid-box-hidable clickable">
                  <span v-bind:class="game.preloaded ? 'font-italic' : ''">
                    <GameNameHeader v-bind:game="game" />
                  </span>
              </div>
              <GameActionIcons v-bind:game="game" class="inverted-action-icons"/>
            </div>
          </GameModalLink>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import _ from 'lodash'
import {mapState} from 'vuex'
import {mapActions} from 'vuex'
import {mapGetters} from 'vuex'
import LockIcon from 'vue-material-design-icons/Lock'
import KeyIcon from 'vue-material-design-icons/Key'
import GameActionIcons from './GameActionIcons'
import GameNameHeader from './GameNameHeader'
import GameModalLink from './GameModalLink'

let PAGE_COUNT_TABLE = 100
let PAGE_COUNT_GRID = 50

export default {
  name: 'GameList',
  components: {GameActionIcons, GameNameHeader, GameModalLink, KeyIcon, LockIcon},
  props: {},
  data() {
    return {
      visuallyLoadedGamesCount: PAGE_COUNT_TABLE
    }
  },
  created() {
    this.$store.dispatch('fetchGamesData')
  },
  watch: {
    showGamesAsTable: 'resetVisuallyLoadedGamesCount',
    sortByKey: 'resetVisuallyLoadedGamesCount',
    sortByDir: 'resetVisuallyLoadedGamesCount'
  },
  computed: {
    ...mapState([
      'loggedIn',
      'showPreloadColumn',
      'showSizeColumn',
      'showLanguagesColumn',
      'showLikeChangedAtColumn',
      'showLikesColumn',
      'bShowPreloads',
      'bGrabDLC',
      'showPlayerCount',
      'showReleaseDate',
      'showGamesAsTable',
      'sortByDir',
      'sortByKey',
      'hideNavBar'
    ]),
    ...mapGetters(['shownGames', 'visuallyLoadedShownGames', 'currentSwitchData', 'regionsFound']),
    visuallyLoadedShownGames: function() {
      return (this.shownGames || []).slice(0, this.visuallyLoadedGamesCount)
    }
  },
  methods: {
    // ...mapMutations([]),
    ...mapActions(['fetchGamesData', 'toggleWantedGame']),
    setSortBy(key) {
      if (this.sortByKey === key) {
        this.$store.commit('setSortByKeyDir', [key, this.sortByDir === 'desc' ? 'asc' : 'desc'])
      } else {
        this.$store.commit('setSortByKeyDir', [key, key === 'name' ? 'asc' : 'desc'])
      }
    },
    toggleBlacklisted() {},
    // googleGameName: function(name) {
    //   let url = `https://www.google.com/search?q=${encodeURIComponent(name)} game`
    //   window.open(url, '_blank')
    // },
    openEShopPage: function(game) {
      let url
      if (game.region) {
        url = `https://ec.nintendo.com/apps/${game.id}/${game.region}`
      } else {
        url = `https://www.nintendo.com/search/#/results/${encodeURIComponent(game.name)}`
      }
      window.open(url, '_blank')
    },
    visuallyLoadMoreGames: function() {
      if (this.visuallyLoadedGamesCount < _.size(this.shownGames)) {
        this.visuallyLoadedGamesCount += this.showGamesAsTable ? PAGE_COUNT_TABLE : PAGE_COUNT_GRID
        console.log('visuallyLoadMoreGames ', this.visuallyLoadedGamesCount)
      }
    },
    resetVisuallyLoadedGamesCount() {
      console.log('resetVisuallyLoadedGamesCount')
      this.visuallyLoadedGamesCount = this.showGamesAsTable ? PAGE_COUNT_TABLE : PAGE_COUNT_GRID
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.hidden {
  display: none;
}

.button-holder {
  width: 100%;
  background: white;
  z-index: 100;
}

.no-button-right-gap button:first-child {
  margin-right: 0;
}
.dropdown-checkbox {
  margin-left: 5px;
}

.game-buttons-form {
  flex-wrap: nowrap;
  justify-content: space-between;
}
.game-buttons-form-ui-holder {
  display: inline-flex;
}

.game-buttons button,
.game-buttons input {
  margin-right: 5px;
}

.game-buttons .grab-missing-container {
  border: 1px solid black;
}

.game-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: flex-start;
}

.game-grid-box {
  position: relative;
  margin: 3px;
  background-color: #e3e3e3;
}
.game-grid-box-placeholder {
  position: absolute;
  top: 20px;
  left: 0;
  right: 0;
  text-align: center;
  font-weight: bold;
}
.game-grid-box-image {
  position: relative;
  width: 256px;
  height: 256px;
  margin: 4px;
  background-repeat: no-repeat;
  background-size: contain;
}
.game-grid-box .game-name {
  position: absolute;
  right: 0;
  left: 0;
  bottom: 31px;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.7);
  text-align: center;
  font-weight: bold;
}
.game-grid-box .game-preload {
  position: absolute;
  left: 0;
  top: 0;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.5);
  color: white;
  text-align: center;
  font-weight: bold;
  padding: 0 3px;
}
.inverted {
  filter: invert(100%);
}
.game-grid-box .game-players {
  position: absolute;
  right: 0;
  top: 0;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.5);
  color: white;
  text-align: center;
  font-weight: bold;
  padding: 0 3px;
}
.game-grid-box .actions-container {
  position: absolute;
  bottom: 2px;
  background: rgba(0, 0, 0, 0.5);
  right: 0;
  left: 0;
  justify-content: center;
  padding-bottom: 3px;
  padding-top: 2px;
}
.game-grid-box .actions-container .action-item {
  filter: invert(100%);
}

.game-grid-box .game-grid-box-hidable {
  visibility: hidden;
}

.game-grid-box:hover .game-grid-box-hidable {
  visibility: visible;
}

.games-table-holder {
  width: 100%;
  height: 100vh;
  overflow-y: scroll;
  overflow-x: hidden;
}

.games-table {
  width: 100%;

  .table-left {
    text-align: left;
    padding-left: 5px;
    padding-right: 5px;
  }
  .table-right {
    text-align: right;
    padding-left: 10px;
    padding-right: 10px;
  }
}

.games-table .status-color:nth-child(odd) {
  background-color: #ebebeb;
}
.games-table .status-color.wanted,
.game-grid-box.wanted {
  background-color: #e04a58;
}
.games-table .status-color.wanted:nth-child(odd) {
  background-color: #d83e4d;
}
.games-table .status-color.outdated,
.game-grid-box.outdated {
  background-color: #cab800;
}
.games-table .status-color.outdated:nth-child(odd) {
  background-color: #dcc500;
}
.games-table .status-color.dlc-needed,
.game-grid-box.dlc-needed {
  background-color: #43b6d0;
}
.games-table .status-color.dlc-needed:nth-child(odd) {
  background-color: #3ca2b9;
}
.games-table .status-color.preloaded,
.game-grid-box.preloaded {
  background-color: #00a912;
}
.games-table .status-color.preloaded:nth-child(odd) {
  background-color: #009a11;
}
.games-table .status-color.unlockable,
.game-grid-box.unlockable {
  background-color: #79f500;
}
.games-table .status-color.unlockable:nth-child(odd) {
  background-color: #77ff00;
}
.games-table .status-color.current,
.game-grid-box.current {
  background-color: #00ca16;
}
.games-table .status-color.current:nth-child(odd) {
  background-color: #00df18;
}
.games-table .status-color.future,
.game-grid-box.future {
  background-color: #8a87d4;
}

.dlc-column {
  text-align: center;
}
.dlc-column .wanted-count {
  font-weight: bold;
  color: #ff7800;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}
.dlc-column .outdated-count {
  font-weight: bold;
  color: #f7c637;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}
.dlc-column .not-unlockable-count {
  font-weight: bold;
  color: #dadada;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}
.dlc-column .unlockable-count {
  font-weight: bold;
  color: #3eecff;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

.games-table td {
  border-right: 1px #565656 solid;
  border-bottom: 1px #565656 solid;
}

.game-row:hover {
  filter: brightness(80%);
}

.actions-container {
  display: flex;
  padding-left: 10px;
}

.action-item {
  flex: 0 0 20px;
  width: 20px;
  height: 20px;
  margin-right: 15px;
  margin-top: -5px;
}

.action-item:last-child {
  margin-right: 0;
}
</style>
