<template>
  <span class="actions-container game-grid-box-hidable">
      <span class="action-item clickable" title="Like" @click.prevent.stop="toggleLikeGame(1)">
        <thumb-up-icon title="Like" v-if="likeInt === 1" />
        <thumb-up-outline-icon  title="Like" v-else/>
      </span>
      <span class="action-item clickable" title="Dislike" @click.prevent.stop="toggleLikeGame(-1)">
        <thumb-down-icon title="Dislike" v-if="likeInt === -1" />
        <thumb-down-outline-icon  title="Dislike" v-else/>
      </span>
    </span>
</template>

<script>
import {mapActions} from 'vuex'
import CheckIcon from 'vue-material-design-icons/Check'
import KeyIcon from 'vue-material-design-icons/Key'
import InformationOutlineIcon from 'vue-material-design-icons/InformationOutline'
import CloseIcon from 'vue-material-design-icons/Close'
import ThumbDownOutlineIcon from 'vue-material-design-icons/ThumbDownOutline'
import ThumbUpOutlineIcon from 'vue-material-design-icons/ThumbUpOutline'
import ThumbUpIcon from 'vue-material-design-icons/ThumbUp'
import ThumbDownIcon from 'vue-material-design-icons/ThumbDown'
export default {
  name: 'GameActionIcons',
  components: {
    ThumbDownIcon,
    ThumbUpIcon,
    ThumbUpOutlineIcon,
    ThumbDownOutlineIcon,
    CloseIcon,
    InformationOutlineIcon,
    CheckIcon,
    KeyIcon
  },
  props: ['game'],
  data() {
    return {
      // this is to make it feel snappier by immediately showing what you clicked before every game is re-computed
      likeIntOverwrite: null
    }
  },
  computed: {
    likeInt() {
      return _.isNumber(this.likeIntOverwrite) ? this.likeIntOverwrite : this.game.likeInt
    }
  },
  watch: {
    game: 'resetLikeIntOverwrite'
  },
  methods: {
    ...mapActions(['setLikeGame']),
    toggleLikeGame(likeInt) {
      if (this.likeInt === likeInt) {
        this.likeIntOverwrite = 0
        this.setLikeGame({game: this.game, likeInt: 0})
      } else {
        this.likeIntOverwrite = likeInt
        this.setLikeGame({game: this.game, likeInt})
      }
    },
    resetLikeIntOverwrite() {
      this.likeIntOverwrite = null
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.action-item {
  margin-right: 5px;
}
</style>
