<template>
  <div v-if="!hideNavBar">
    <b-navbar toggleable="md" type="dark" variant="secondary">

      <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

      <b-navbar-brand href="#/about">Doge</b-navbar-brand>

      <b-collapse is-nav id="nav_collapse">

        <b-navbar-nav>
          <!--<router-link to="/">Games</router-link> |-->
          <!--<router-link to="/about">About</router-link>-->
          <b-nav-item href="#/">Games ({{shownGames.length}})</b-nav-item>
          <b-nav-item href="#/history">History</b-nav-item>
          <b-nav-item href="#/showcase">Showcase</b-nav-item>
          <b-nav-item href="#/about">About</b-nav-item>
          <!--<b-nav-item href="#/stuff" disabled>Disabled</b-nav-item>-->
        </b-navbar-nav>

        <!-- Right aligned nav items -->
        <b-navbar-nav class="ml-auto">

          <b-nav-form>
            <b-btn @click="toggleView" title="Toggle View" class="btn btn-secondary btn-sm">
              <span v-if="showGamesAsTable">
                <image-icon title="Toggle View"/>
              </span>
              <span v-else>
                <view-list-icon  title="Toggle View"/>
              </span>
            </b-btn>
            <b-btn v-b-modal="'modalColumns'" title="Columns" class="btn btn-secondary btn-sm">
              <view-column-icon title="Columns" />
            </b-btn>
            <b-btn v-b-modal="'modalFilters'" title="Filters" class="btn btn-secondary btn-sm">
              <filter-icon title="Filters" />
            </b-btn>
            <b-form-input size="sm" class="mr-sm-2" type="text" placeholder="Search" v-model="search"/>
            <b-form-select v-if="hasSwitchOptions" v-model="currentSwitchId" :options="switchIdOptions" class="my-2 my-sm-0" size="sm" />
            <!--<b-button size="sm" class="my-2 my-sm-0" type="submit">Search</b-button>-->
          </b-nav-form>

          <!--<b-nav-item-dropdown text="Lang" right>-->
            <!--<b-dropdown-item href="#">EN</b-dropdown-item>-->
            <!--<b-dropdown-item href="#">ES</b-dropdown-item>-->
            <!--<b-dropdown-item href="#">RU</b-dropdown-item>-->
            <!--<b-dropdown-item href="#">FA</b-dropdown-item>-->
          <!--</b-nav-item-dropdown>-->

          <b-nav-item-dropdown right>
            <!-- Using button-content slot -->
            <template slot="button-content">
              <em><span :class="isAdmin ? 'is-admin' : ''">{{username}}</span></em>
            </template>
            <!--<b-dropdown-item href="#">Profile</b-dropdown-item>-->
            <b-dropdown-item href="#" @click="logout">Signout</b-dropdown-item>
            <b-dropdown-item href="#" @click="toggleHideNavBar">Fullscreen</b-dropdown-item>
            <b-dropdown-item href="#" @click="exportGamesList">Export Game List</b-dropdown-item>
          </b-nav-item-dropdown>
        </b-navbar-nav>

      </b-collapse>
    </b-navbar>

    <!-- modalColumns -->
    <b-modal id="modalColumns" title="Columns" size="sm" ok-only>
        <div class="text-left">
          <b-form-checkbox v-model="showPlayerCount">
            Player Count
          </b-form-checkbox>
        </div>
        <div class="text-left">
          <b-form-checkbox v-model="showLanguagesColumn">
            Languages
          </b-form-checkbox>
        </div>
        <div class="text-left">
          <b-form-checkbox v-model="showReleaseDate">
            Release Date
          </b-form-checkbox>
        </div>
        <div class="text-left">
          <b-form-checkbox v-model="bGrabDLC">
            DLC
          </b-form-checkbox>
        </div>
      <div class="text-left">
        <b-form-checkbox v-model="showSizeColumn">
          Size
        </b-form-checkbox>
      </div>
      <div class="text-left" v-if="knowAnyGameKey || showPreloadColumn">
        <b-form-checkbox v-model="showPreloadColumn">
          Key
        </b-form-checkbox>
      </div>
      <div class="text-left">
        <b-form-checkbox v-model="showLikeChangedAtColumn">
          Liked At
        </b-form-checkbox>
      </div>
      <div class="text-left">
        <b-form-checkbox v-model="showLikesColumn">
          Likes
        </b-form-checkbox>
      </div>
    </b-modal>

    <!-- modalFilters -->
    <b-modal id="modalFilters" title="Filter Games" size="lg" ok-only>
      <div class="text-left">
        <b-form-group label="">
          <b-form-radio-group buttons
                              button-variant="outline-primary"
                              size="sm"
                              v-model="filterWantedStatus"
                              :options="optionsHaveWant" />
        </b-form-group>
      </div>
      <div class="text-left">
        <b-form-group label="">
          <b-form-radio-group buttons
                              button-variant="outline-primary"
                              size="sm"
                              v-model="filterLikeStatus"
                              :options="optionsLikeStatus" />
        </b-form-group>
      </div>
      <div class="text-left">
        <b-form-group label="">
          <b-form-radio-group buttons
                              button-variant="outline-primary"
                              size="sm"
                              v-model="filterDemoStatus"
                              :options="optionsDemoStatus" />
        </b-form-group>
      </div>
      <div class="text-left">
        <b-container fluid>
          <b-row class="my-1">
            <b-col sm="3"><label>Min Players:</label></b-col>
            <b-col sm="3"><b-form-input :type="'number'" v-model="filterPlayersMin"></b-form-input></b-col>
            <b-col sm="3"><label>Max Players:</label></b-col>
            <b-col sm="3"><b-form-input :type="'number'" v-model="filterPlayersMax"></b-form-input></b-col>
          </b-row>
        </b-container>
      </div>
      <div class="text-left">
        <b-form-checkbox v-model="filterInstalled">
          Only Installed
        </b-form-checkbox>
      </div>
      <div class="text-left" v-if="knowAnyGameKey || !bShowPreloads">
        <b-form-checkbox v-model="bShowPreloads">
          Show Keyless
        </b-form-checkbox>
      </div>
      <div class="text-left" v-if="anyGameBlacklisted">
        <b-form-checkbox v-model="filterShowBlacklisted">
          Show Blacklisted
        </b-form-checkbox>
      </div>
      <div class="text-left" style="margin-top: 10px;">
        <div>Include Regions:</div>
        <b-form-checkbox-group name="regionsSelected" v-model="regionsSelected" :options="regionOptions">
        </b-form-checkbox-group>
      </div>
      <div class="text-left" style="margin-top: 10px;">
        <div>Require Languages:</div>
        <b-form-checkbox-group name="languagesSelected" v-model="languagesSelected" :options="languageOptions">
        </b-form-checkbox-group>
      </div>
    </b-modal>

  </div>
</template>

<script>
import {mapState} from 'vuex'
import {mapActions} from 'vuex'
import {mapMutations} from 'vuex'
import {mapGetters} from 'vuex'
import ViewColumnIcon from 'vue-material-design-icons/ViewColumn'
import FilterIcon from 'vue-material-design-icons/Filter'
import ImageIcon from 'vue-material-design-icons/Image'
import ViewListIcon from 'vue-material-design-icons/ViewList'

function downloadText(filename, text) {
  var element = document.createElement('a');
  element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text));
  element.setAttribute('download', filename);

  element.style.display = 'none';
  document.body.appendChild(element);

  element.click();

  document.body.removeChild(element);
}

export default {
  name: 'NavBar',
  components: {ViewListIcon, ImageIcon, FilterIcon, ViewColumnIcon},
  props: {},
  data() {
    return {
      optionsHaveWant: [
        {text: 'Any', value: ''},
        {text: 'Want + Have', value: 'haveWant'},
        {text: 'Have', value: 'have'},
        {text: 'Missing', value: 'missing'},
        {text: 'Unwanted', value: 'unwanted'}
      ],
      optionsLikeStatus: [
        {text: 'Any', value: ''},
        {text: 'Liked', value: 'liked'},
        {text: 'Neutral', value: 'neutral'},
        {text: 'Not Disliked', value: 'notDisliked'}
      ],
      optionsDemoStatus: [{text: 'Any', value: ''}, {text: 'No Demos', value: 'noDemos'}, {text: 'Only Demos', value: 'onlyDemos'}]
    }
  },
  computed: {
    ...mapState(['loggedIn', 'isAdmin', 'username', 'showGamesAsTable', 'switchDataBySwitchId', 'filterHideRegions', 'hideNavBar']),
    ...mapGetters(['regionsFound', 'languagesFound', 'shownGames', 'knowAnyGameKey', 'anyGameBlacklisted']),
    hasSwitchOptions() {
      return _.size(this.switchDataBySwitchId) > 0
    },
    switchIdOptions() {
      let options = _.map(_.keys(this.switchDataBySwitchId), function(id) {
        return {value: id, text: id}
      })
      return [{value: null, text: 'Pick a Switch'}].concat(options)
    },
    regionOptions() {
      return _.map(this.regionsFound, function(region) {
        return {value: region, text: region}
      })
    },
    languageOptions() {
      return _.map(this.languagesFound, function(language) {
        return {value: language, text: language}
      })
    },
    regionsSelected: {
      get() {
        return _.without(this.regionsFound, ...this.filterHideRegions)
      },
      set(picked) {
        let hidden = _.without(this.regionsFound, ...picked)
        if (_.size(hidden) !== _.size(this.filterHideRegions)) {
          // needed?
          this.$store.commit('set', {filterHideRegions: hidden})
        }
      }
    },
    languagesSelected: {
      get() {
        return this.$store.state.filterLanguagesRequired
      },
      set(picked) {
        this.$store.commit('set', {filterLanguagesRequired: picked})
      }
    },
    search: {
      get() {
        return this.$store.state.search
      },
      set(search) {
        this.$store.commit('set', {search})
      }
    },
    currentSwitchId: {
      get() {
        return this.$store.state.currentSwitchId
      },
      set(currentSwitchId) {
        this.$store.commit('set', {currentSwitchId})
      }
    },
    showPlayerCount: {
      get() {
        return this.$store.state.showPlayerCount
      },
      set(showPlayerCount) {
        this.$store.commit('set', {showPlayerCount})
      }
    },
    showReleaseDate: {
      get() {
        return this.$store.state.showReleaseDate
      },
      set(showReleaseDate) {
        this.$store.commit('set', {showReleaseDate})
      }
    },
    bGrabDLC: {
      get() {
        return this.$store.state.bGrabDLC
      },
      set(bGrabDLC) {
        this.$store.commit('set', {bGrabDLC})
      }
    },
    showPreloadColumn: {
      get() {
        return this.$store.state.showPreloadColumn
      },
      set(showPreloadColumn) {
        this.$store.commit('set', {showPreloadColumn})
      }
    },
    showSizeColumn: {
      get() {
        return this.$store.state.showSizeColumn
      },
      set(showSizeColumn) {
        this.$store.commit('set', {showSizeColumn})
      }
    },
    showLanguagesColumn: {
      get() {
        return this.$store.state.showLanguagesColumn
      },
      set(showLanguagesColumn) {
        this.$store.commit('set', {showLanguagesColumn})
      }
    },
    showLikeChangedAtColumn: {
      get() {
        return this.$store.state.showLikeChangedAtColumn
      },
      set(showLikeChangedAtColumn) {
        this.$store.commit('set', {showLikeChangedAtColumn})
      }
    },
    showLikesColumn: {
      get() {
        return this.$store.state.showLikesColumn
      },
      set(showLikesColumn) {
        this.$store.commit('set', {showLikesColumn})
      }
    },
    filterWantedStatus: {
      get() {
        return this.$store.state.filterWantedStatus
      },
      set(filterWantedStatus) {
        this.$store.commit('set', {filterWantedStatus})
      }
    },
    filterLikeStatus: {
      get() {
        return this.$store.state.filterLikeStatus
      },
      set(filterLikeStatus) {
        this.$store.commit('set', {filterLikeStatus})
      }
    },
    filterDemoStatus: {
      get() {
        return this.$store.state.filterDemoStatus
      },
      set(filterDemoStatus) {
        this.$store.commit('set', {filterDemoStatus})
      }
    },
    bShowPreloads: {
      get() {
        return this.$store.state.bShowPreloads
      },
      set(bShowPreloads) {
        this.$store.commit('set', {bShowPreloads})
      }
    },
    filterInstalled: {
      get() {
        return this.$store.state.filterInstalled
      },
      set(filterInstalled) {
        this.$store.commit('set', {filterInstalled})
      }
    },
    filterShowBlacklisted: {
      get() {
        return this.$store.state.filterShowBlacklisted
      },
      set(filterShowBlacklisted) {
        this.$store.commit('set', {filterShowBlacklisted})
      }
    },
    filterPlayersMin: {
      get() {
        return this.$store.state.filterPlayersMin
      },
      set(filterPlayersMin) {
        filterPlayersMin = _.parseInt(filterPlayersMin)
        this.$store.commit('set', {filterPlayersMin})
      }
    },
    filterPlayersMax: {
      get() {
        return this.$store.state.filterPlayersMax
      },
      set(filterPlayersMax) {
        filterPlayersMax = _.parseInt(filterPlayersMax)
        this.$store.commit('set', {filterPlayersMax})
      }
    }
  },
  methods: {
    ...mapMutations(['toggleHideNavBar']),
    ...mapActions(['login', 'logout']),
    toggleView() {
      this.$store.commit('set', {showGamesAsTable: !this.showGamesAsTable})
    },
    exportGamesList(){
      let out = ''
      _.each(this.shownGames, function(game) {
        out += `${game.name} [${game.id}][v${game.latestVersion}]\r\n`
        _.each(game.dlcs, function(dlc) {
          out += `${game.name}: ${dlc.name} [${dlc.dlcId}][v${dlc.latestVersion}]\r\n`
        })
      })
      downloadText('games.txt',out)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .is-admin {
    color: #a7d0a7;
  }
</style>
